




























import Vue from 'vue';
import Tabs from '@/components/Tabs.vue';
import Chart from '@/components/Chart.vue';
import {Component,Watch} from 'vue-property-decorator';
import recordTypeList from '@/constants/recordTypeList.ts';
import clone from '@/lib/clone.ts';
import dayjs from 'dayjs';
import _ from 'lodash'

@Component({
    components: {Tabs, Chart}
})
export default class Statistics extends Vue {
    type = '-';
    recordTypeList = recordTypeList;

    get recordList() {
        // eslint-disable-next-line no-undef
        return (this.$store.state as RootState).recordList;
        //    as断言类型,现在custom.d.ts里
    }

    get groupedList() {
        const {recordList} = this;
        if (recordList.length === 0) {
            return [];
        }
        const newList = clone(recordList)
            .filter(r => r.type === this.type)
            //    返回与type一致的数组'-'支出 '+' 收入
            .sort((a, b) => {
                return dayjs(b.date).valueOf() - dayjs(a.date).valueOf();
            });
        //排序将日期从后到前排

        // eslint-disable-next-line no-undef
        type Result = { title: string, total?: number, items: RecordItem[] }[]
        if (!newList || newList.length == 0) {
            return false;
        }
        const result: Result = [{title: dayjs(newList[0].date).format('YYYY-MM-DD'), items: [newList[0]]}];

        for (let i = 1; i < newList.length; i++) {
            const current = newList[i];
            const last = result[result.length - 1];
            if (dayjs(last.title).isSame(dayjs(current.date), 'day')) {
                last.items.push(current);
            } else {
                result.push({title: dayjs(current.date).format('YYYY-MM-DD'), items: [current]});
            }
        }

        result.map(group => {
            group.total = group.items.reduce((sum, item) => {
                return sum + item.amount;
            }, 0);
            //使用reduce的第二个参数是为了防止累计操作时数组为空时出错
            //把同一天的值累加给每日汇总
        });
        console.log('测试运行')
        return result;
    }
    get axisData(){
        const today = new Date()
        const array = []
        for(let i= 29;i>=0;i--){
            const dateString = dayjs(today).subtract(i,'day').format('YYYY-MM-DD')
            // @ts-ignore
            const found = _.find(this.groupedList,{title:dateString})
            array.push({date: dateString.substr(-5),value:found?found.total:0})
        }

        return array


    }
    @Watch('axisData')
    initOption() {

         let xAxis = this.axisData.map((item)=>{
             return item.date
        })
        let xValue = this.axisData.map((item)=>{
            return item.value
        })

        return {
            grid: {
                top: 20,
                right: 0,
                bottom: 20,
                left: 0,
            },
            xAxis: {
                type: 'category',
                data: xAxis,
                axisTick: {
                    alignWithLabel: true
                },
                axisLine: {lineStyle:{color: '#666'}}
            },
            yAxis: {
                type: 'value',
                show: false,
            },
            series: [{
                data: xValue,
                type: 'line',
                symbol: 'circle',
                symbolSize: 10,
                itemStyle:{borderwidth:1,color: '#666'}
            }],
            tooltip: {
                show: true,
                triggerOn: 'click',
                position: 'top',
                confine: 'true',
                trigger: 'axis'
                // formatter: '{c}'
            }
        };
    }

    // eslint-disable-next-line no-undef
    tagString(tags: Tag[]) {
        let formatTag = tags.map(tag => {
            return tag.name;
        });
        return tags.length === 0 ? '无' : formatTag.join(',');
    }

    //tagList长度为0的时候表示标签没有记录，返回无，否则用逗号隔开返回标签


    beautify(string: string) {
        // console.log('string---',string)
        const day = dayjs(string);
        const now = dayjs();

        if (day.isSame(now, 'day')) {
            return '今天';
        } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
            return '昨天';
        } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
            return '前天';
        } else if (day.isSame(now, 'year')) {
            return day.format('M月D日');
        } else {
            return day.format('YYYY年M月D日');
        }
    }

    beforeCreate() {
        this.$store.commit('fetchRecords');
    }

    mounted() {
        const chartWrapper = this.$refs.chartWrapper as HTMLDivElement;
        chartWrapper.scrollLeft = chartWrapper.scrollWidth
    //一进页面就进行滚动的dom操作
    }

}
