






















import Vue from 'vue'
import {Component,Prop} from 'vue-property-decorator'
@Component
export default class NumberPad extends Vue{
    @Prop() readonly value!: number;
    //!为空不警告
    output = this.value.toString();
    currentButton = ''
    get parse(){
        return parseFloat(this.output)
    }
    control(event:MouseEvent){
        const button = (event.target as HTMLButtonElement)
        const input = button.innerText!
        //忽略类型，防止提示
        if(input === '删除'){
            this.remove()
        }else if(input === '清空'){
            this.clear()
        }else if(input === 'OK'){
            this.ok()
        }
        this.numInput(input)
        this.selectButton(input)
    }
    numInput(inputButton:string){
        if(this.output.indexOf('.')>=0&&inputButton==='.'){return}
        if(this.output.length>=16){return;}
        if('0123456789.'.indexOf(inputButton)>=0){
            if(this.output==='0'&&inputButton!=='.'){
                this.output = inputButton
                return
            }
            this.output+=inputButton
            return
        }
    }
    remove(){
        if(this.output.length===1){
            this.output = '0'
            return
        }
        this.output = this.output.slice(0,-1)
    }
    clear(){
        this.output = '0'
    }
    ok(){
        this.$emit('update:value',this.parse)
        this.$emit('submit',this.parse)
        this.clear()
    }
    selectButton(button:string){
        this.currentButton = button
        setTimeout(()=>{
            this.currentButton = ''
        },200 )
    }

}
